import React, { useEffect } from "react";

import { graphql, useStaticQuery } from "gatsby";
import { Helmet } from 'react-helmet';

import Layout from "../components/layout_inner";
import CoverImg from "../components/cover_img";

export default (props) => {
  const data = useStaticQuery(graphql`
    query {
      allFile(
        filter: { relativePath: { glob: "posts/interiors/**/*.jpg" } },
        sort: { fields: base, order: ASC }
      ) {
        edges {
          node {
            relativeDirectory
            childImageSharp {
              fluid(quality: 85, maxWidth: 1280) {
                ...GatsbyImageSharpFluid_withWebp_tracedSVG
              }
            }
          }
        }
      }
    }
  `);
  const images = data.allFile.edges.reduce((acc, { node }) => {
    const dirs = node.relativeDirectory.replace('posts/interiors/', '').split('/');

    acc[dirs[0]] = acc[dirs[0]] || {};
    acc[dirs[0]][dirs[1]] = acc[dirs[0]][dirs[1]] || [];
    acc[dirs[0]][dirs[1]].push(node.childImageSharp.fluid);

    return acc;
  }, {});

  const studios = [{
    name: "Студия Воздух",
    address: "Куйбышева 22, 3 этаж",
    areas: [{
      name: 'Зал Основной',
      images: images.vozduh.default,
    }]
  }, {
    name: "Forest Studio",
    address: "г. Минск, ул. Маяковского 115, 3 этаж, 310 помещение.",
    areas: [{
      images: images.forest.default,
    }]
  }, {
    name: "Загородная Студия White House",
    address: "д. Тарасово, ул. Энергетиков, 3",
    areas: [{
      images: images.white_house.default,
    }]
  }, {
    name: "Студия АртЛофт",
    address: "Куйбышева, 22; 6 корпус, 4 1/2 этаж",
    areas: [{
      images: images.artloft.default,
    }]
  }, {
    name: "Студия Lightstudio",
    address: "Веры Хоружей  29, 8 этаж",
    areas: [{
      name: "Зал  Light",
      images: images.light.light,
    }, {
      name: "Зал Flat",
      images: images.light.flat,
    }]
  }, {
    name: "Студия Счастье",
    address: "Куйбышева 22. 4 c половиной этаж, до конца коридора",
    areas: [{
      images: images.happiness.default,
    }]
  }, {
	name: "Студия BLENDA STUDIO",
    address: "пр. Машерова, 11",
    areas: [{
      name: "Зал  LIFE",
      images: images['BLENDA STUDIO'].LIFE,
    }, {
      name: "Зал TIME",
      images: images['BLENDA STUDIO'].TIME,
	  }, {
      name: "Зал VOGUE",
      images: images['BLENDA STUDIO'].VOGUE,
    }]
  }, {
    name: "Студия Молоко",
    address: "Толбухина 4, 3 этаж",
    areas: [{
      name: "Зал Кристалл",
      images: images.Moloko.kristal,
    }, {
      name: "Зал Милена",
      images: images.Moloko.mil,
    }, {
      name: "Зал Графит",
      images: images.Moloko.grafit,
    }]
  }, {
    name: "Студия Феломена",
    address: "Казинца 11а, крыло Б, 2 этаж",
    areas: [{
      name: "Зал White",
      images: images.Fel.white,
    }, {
      name: "Зал Loft",
      images: images.Fel.loft,
    }, {
      name: "Зал Family",
      images: images.Fel.bavy,
    }]
  }, {
    name: "Студия Катафот",
    address: "Кнорина 55",
    areas: [{
      images: images.Kat.default,
    }]
  }, {
    name: "Студия Lirika",
    address: "ФАБРИЦИУСА 8 (3 ЭТАЖ)",
    areas: [{
      images: images.lirika.default,
    }]
  }, {
    name: "Студия Smart Studio",
    address: "Строителей 1",
    areas: [{
      name: "Зал Подиум",
      images: images.smart_studio.pod,
    }, {
      name: "Зал Винтаж",
      images: images.smart_studio.vint,
    }]
  }, {
    name: "Студия ETO",
    address: "пр-т Партизанский 2/1, 4 этаж",
    areas: [{
      name: "Double room",
      images: images.ETO.DOUBLE,
    }, {
      name: "Зал White",
      images: images.ETO.w,
    }]
  }, {
    name: "Dreamstudio",
    address: "ул. Волгоградская 8,1-й этаж, Администратор в конце коридора",
    areas: [{
      name: "Family",
      images: images.Dream.family,
    }, {
      name: "Прованс",
      images: images.Dream.provans,
    }]
  }, {
    name: "Студия Diva",
    address: "ул. Октябрьская 10 Б ",
    areas: [{
      name: "Light",
      images: images.diva.light,
    }, {
      name: "Loft",
      images: images.diva.Loft,
    }, {
      name: "Plaza",
      images: images.diva.plaza,
    }, {
      name: "Royal",
      images: images.diva.r,
    }]
  }];

  useEffect(() => {
    // workaround glightbox not supporting ssr
    import('glightbox').then(({ default: GLightbox }) => {
      GLightbox({
        selector: 'a.photo',
        type: 'image'
      });
    });
  });

  return (
    <Layout {...props}>
      <Helmet title="Фотограф Минск | Cтудии Минска" defer={false} />
      <h1>
        Студии Минска
      </h1>
      <div className="post">
        <div className="post-content">
          {studios.map(studio => (
            studio.areas.map((area, index) => (
              <div key={`${studio.name}-${area.name}`} className="clear">
                <p style={{ paddingTop: '28px' }}>
                  {studio.name}<br/>
                  {!index ? (<span>Адрес: {studio.address}<br /></span>) : ''}
                  {area.name}
                </p>
                <div className="cards">
                  {area.images.map(fluid => (
                    <a
                      key={fluid.src}
                      rel="gallery"
                      className="card photo"
                      style={{ margin: '1.5% 0 1.5% 3%'}}
                      href={fluid.src}
                    >
                      <span className="thumbnail">
                        <CoverImg fluid={fluid} />
                      </span>
                    </a>
                  ))}
                </div>
              </div>
            ))
          ))}
        </div>
        <div style={{ marginBottom: "1.5%" }} className="clear"></div>
      </div>
    </Layout>
  );
};
